import React, { Component, Suspense } from 'react';
import DisplayUrl from '../../../PublishCommonComponents/DisplayUrl';
import { DisplayURL } from '../../../../../Services/Api'
class ChromecastUrl extends Component {

    render() {
        const { data } = this.props
        return <div className="card text-decoration-none">
            <div className="card-body p-lg-7">
                <div className="m-auto">
                    <div className="embed_code">
                        <h5 className="mb-5 fw-bolder">Steps To Display Social wall With ChromeCast</h5>
                        <h6>Step 1 : Copy Display wall url</h6>
                        <DisplayUrl url={`${DisplayURL}${data && Object.keys(data).length > 0 ? data.wallId : null}`} />
                        <h6 className="mb-5">Step 2 : Cast The Display Wall With Chromecast</h6>
                        <ul className="list-unstyled list-points fs-7">
                            <li className="mb-3">Cast your screen through Google Chromecast and launch your Tagbox URL in the browser.</li>
                            <li className="mb-3">Launch your Tagbox URL in the browser and don't forget to <span className="fw-bold">TURN ON</span> the Fullscreen display.</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    }
}

export default ChromecastUrl;